<template>
  <div class="user-ticket-detail">
    <div class="ticket-content-wrapper">
      <img class="img-responsive" src="../assets/checkDetailTop.png" />
      <div class="ticket-content">
        <div class="ticket-tip">
          <h2>{{prizeNumber}}次</h2>
        </div>
        <div v-for="(item, index) in pumpingChanceList" :key="index">
          <div class="ticket-content-item">
            <div class="content-text">
              <div class="content-tip">
                <span class="ticket-name">{{getTicketType(item.prizeRecordEntity.reason)}}</span>
                <span class="ticket-source">{{item.prizeRecordEntity.recordTime}}</span>
              </div>
            </div>
           <div class="ticket-wrapper">
              <div class="ticket" v-if="item.prizeRecordEntity.type">
                <span>+{{item.prizeRecordEntity.number}}次</span>
              </div>
              <div class="ticket" v-else>-{{item.prizeRecordEntity.number}}次</div>
              <div v-if="!item.prizeRecordEntity.type && item.prizeEntity && item.prizeEntity.name" class="vote-emp">
                <span>获得{{item.prizeEntity.name}}一张</span>
              </div>
            </div>
          </div>
        </div>
        <p class="ticket-no-tip" v-if="noRecordFlag">{{noRecordTip}}</p>
      </div>
    </div>
<!--     <div v-if="more" class="lcj-more" @click="loadBottom">
      <span>查看更多</span>
    </div>
    <div v-if="allLoaded" class="no-more">
      <span>已加载完</span>
    </div> -->
       <div class="heart-tip">
        <p>温馨提示：</p>
        <p>1.客户购买热销基金后，待系统收到基金公司确认信息后方可获取系统发放的抽生肖卡机会；</p>
        <p>2.客户签约专属投资顾问后，待客户关系管理系统确认后方可获取系统发放的抽生肖卡机会；</p>
        <p>3.客户新开通信用或者期权账户后，待系统确认后方可获取抽生肖卡机会，预计为（T+1）个交易日。</p>
        <p>请您密切关注剩余抽卡机会变化。如有疑问请联系所在营业部或拨打新时代证券全国统一客服热线95399咨询。</p>
      </div>
     <div class="gift-tip">
      <p>投资有风险 入市需谨慎</p>
    </div>
  </div>
</template>
<script>

import { getMyPrizeRecord } from '@/api/prize'
import { getTicketNumber, getUserTicketRecord } from '@/api/ticket'
import { Toast, Indicator } from 'mint-ui';

export default {
  name: 'ticketDetail',
  components: {
  },
  data() {
    return {
      pumpingChanceList: [], 
      more: false,
      allLoaded: false,
      noRecordFlag: false,
      noRecordTip: '暂时没有中奖记录',
      ticketPageNumber: 0
    }
  },
  computed: {
    count() {
      return this.$store.state.count;
    },
    prizeNumber() {
      return this.$store.state.prizeNumber
    }
  },
  created() {
    let titleObject = {
      back: true,
      romoveMarginTop: false,
      title: "查看明细"
    }
    this.$emit("routerChange", titleObject);
    Indicator.open({
      text: '加载中...',
      spinnerType: 'triple-bounce'
    });
    //this.getTicketNumber();
   // this.getUserTicketRecord();
    this.getMyPrizeRecord();
  },
  methods: {
    getTicketType(type) {
      if (type == "11") {
        return '登录';
      } else if (type == "12") {
        return '分享';
      } else if (type == "13") {
        return '购买理财'
      } else if (type == "14") {
        return '开通信用账户'
      } else if (type == "15") {
        return '开通期权账户'
      } else if (type == "16") {
        return '签约投顾'
      } else if (type == "01") {
        return '大转盘抽生肖卡'
      }
    },

    loadBottom: function () {
      console.log("loadBottom");
      this.ticketPageNumber += 1;
      this.getUserTicketRecord();
    },

    getMyPrizeRecord() {
      Indicator.open({
        text: '加载中...',
        spinnerType: 'triple-bounce'
        });
        getMyPrizeRecord().then((res) => {
        console.log(res);
        Indicator.close();
        if (res.data.resCode == 0) {
          //成功请求数据
          if (res.data.result.length > 0) {
            this.pumpingChanceList = res.data.result;
             console.log("hah");
            console.log(this.pumpingChanceList);
            this.noRecordFlag = false;
          }else {
             this.noRecordFlag = true;
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    }
  }
}
</script>
